.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

nav {
  background-color: #8319b8;
}
nav .navbar-brand {
  font-size: 25px;
}
nav a {
  color: #fafafb;
  font-size: large;
}

.banner {
  padding-left: 0px;
  padding-right: 0px;
}

header {
  background-color: #5a7591;
}

form {
  border: 1px;
  padding: 2%;
  margin-bottom: 2%;
  margin-top: 2%;
  margin-left: 10%;
  margin-right: 10%;
  background-color: #d8dce4;
}

.form-button,
h3 {
  text-align: center;
}

.login-form {
  display: grid;
  align-items: center;
}

.check1,
.check2,
.check3,
.check4 {
  padding: 30px;
}

.Billing_header {
  border-bottom: 1px solid black;
  align-self: center;
}

.pay_now {
  border-top: 1px solid black;
}

@media screen and (min-width: 1024px) {
  .checkout-form {
    display: grid;
    grid-template-rows: 5% 35% 40% 20%;
    padding-top: 2%;
    padding-bottom: 1%;
  }

  .form1 {
    display: grid;
    grid-template-columns: 50% 50%;
    margin-top: 10px;
  }

  .form2 {
    display: grid;
    grid-template-columns: 50% 50%;
  }
}
footer {
  background-color: #8319b8;
  color: #fafafb;
  padding-bottom: 1%;
  padding-top: 1%;
}
footer .about-footer ul,
footer .cart-footer ul,
footer h5,
footer .social-footer ul,
footer .contact-us-footer ul {
  text-align: center;
}
footer .about-footer li,
footer .cart-footer li,
footer .contact li,
footer .social-footer li {
  display: inline;
}
footer a {
  text-decoration: none;
  color: #fafafb;
  padding: 3px;
}
footer .map {
  text-align: center;
}
footer .copyright {
  margin-top: 10px;
}

@media screen and (min-width: 670px) {
  footer .container {
    display: grid;
    grid-template-columns: 20% 20% 30% 30%;
    grid-template-rows: 50% 50%;
  }
  footer .about-footer li,
  footer .cart-footer li {
    display: block;
  }
  footer .cart-footer {
    padding-left: 20%;
  }
  footer .social-footer {
    padding-left: 30%;
  }
  footer .contact-us-footer {
    padding-left: 20%;
  }
  footer .map {
    grid-row: 2/2;
    grid-column: 1/5;
    text-align: end;
  }
}

